	module.exports = {"data":{"markdownRemark":{"html":"<p>早朝に \"Soil Street\"を読み、田舎についての記事を書くことは \"White Deer\"と非常によく似ているようです。村で起こった変化、政治的な変化の下での人々の運命の変化は、政治的な変化の下で、人類が歪められ、すべての歴史が少しばかばかしく、様々な人物の運命が変化の歴史にあるという印象を与えるようです。誤配置、歴史がこれほど変わらないなら、それは良いでしょう。私は田舎の野性的なセクシュアリティの概念をたくさん書いています。私は田舎に深い人生の基盤を持っていませんが、これが完全に真実かどうかは判断できません。しかし、あなたがたくさん書くと、意図的な痕跡があります。そのような小説はもっとあり、風の暴風の後に残されるものは後で見ることができます。</p>\n<p>Wujiaochangの価格を見るのはちょっと上のようだ。ストリートは \"波の音はまだ\"、おそらくカセットの外に満ちています。この歌の歌詞は、気まぐれではなく、ある種の言葉ではなく、書かれています。「この古いチケットが旅客船に搭乗できるかどうかは、昨日の話を繰り返せますか？</p>\n<p>彼が自営業の小さなSに行ったとき、彼は新年に戻っていませんでした。彼は江蘇から上海にやって来て、上海で生活する場所も持っています。近年、上海や大都市で働く人が増えています。外国人で働く人が増えているように、都市の人生はこれからの論理になるのではないでしょうか。開発された地域は常に外部に魅力的です。今年の「移民労働者の潮」が上昇し始めた。大都市の駅は混雑しており、全体的な戦略が必要である。そうしなければ、このような強い社会的圧力を解決することはできない。</p>\n<p>冬季オリンピックが開催され、中国チームはまだ金メダルを獲得していない。</p>","id":"/home/swain/workspace/political-life/src/pages/ja/0037.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ja/0037.md","frontmatter":{"title":"2月20日 日曜日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ja/0037.md absPath of file >>> MarkdownRemark"}}

